/* Contents */

// Home
.home {
  &-masthead {
    position: relative;
    height: 80vh;
    &:before {
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background: linear-gradient(0deg, rgba(2, 32, 45, 0.30) 0%, rgba(2, 32, 45, 0.30) 100%);
      z-index: 3; }
    &-mashteadbg, {
      height: 100%;
      @include imgBg(53% 0 0); }
    &-swiper {
      height: 100%;
      .swiper-slide {
        width: 100%;
        height: 100%;
        @include imgBg(53% 0 0); } }

    &-content {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 4;
      display: flex;
      align-items: flex-end;
      padding-bottom: rem(64);
      color: $white;
      span {
        display: block;
        margin-bottom: rem(4); }
      h1 {
        margin-bottom: rem(40);
        width: 100%;
        max-width: rem(845); } } }

  &-category {
    .section-title {
      padding-top: rem(48);
      border-top: 1px solid $burgundy;
      .row {
        align-items: flex-end;
        .col-lg-6:last-child {
          display: flex;
          justify-content: flex-end; } } }
    &-wrap {
      margin-bottom: rem(-24);
      .col-lg-3 {
        margin-bottom: rem(24); }

      .card-category {
        width: 100%;
        display: block;
        position: relative;
        transition: all .3s ease-in-out;
        overflow: hidden;
        &-img {
          transition: all .3s ease-in-out;
          &:after {
            content: "";
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            background: linear-gradient(359deg, rgba(2, 32, 45, 0.50) 0%, rgba(2, 32, 45, 0.00) 100%);
            z-index: 2;
            transition: all .3s ease-in-out; }
          .bg {
            transition: all .3s ease-in-out; }
          @include imgBg(83.331% 0 0); }

        &-name {
          position: absolute;
          left: 0;
          bottom: 0;
          padding: rem(24);
          width: 100%;
          display: flex;
          justify-content: space-between;
          align-items: flex-end;
          z-index: 3;
          h4 {
            @include font-400;
            margin-bottom: 0;
            color: $white; }
          .icon {
            width: rem(24);
            height: rem(24);
 } }            // transition: all .3s ease-in-out

        &:hover {
          .card-category {
            &-img {
              &:after {
                opacity: .75; }
              .bg {
                transform: scale(1.2); } }
            &-name {
              .icon {
                background: url(../images/ic-arrow-circle-white-filled.svg);
                background-size: rem(20);
                background-position: center;
                background-repeat: no-repeat;
                path {
                  fill: transparent; } } } } } } } }

  &-aboutus {
    position: relative;
    &-bg {
      @include imgBg(100% 0 0);
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      &:after {
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background: linear-gradient(82deg, rgba(0, 0, 0, 0.50) 0%, rgba(0, 0, 0, 0.00) 51.93%); } }
    &-content {
      // position: absolute
      // top: 0
      // left: 0
      // width: 100%
      // height: 100%
      z-index: 3;
      position: relative;
      padding: rem(64) 0;
      min-height: rem(640);
      display: flex;
      .container {
        flex: 1;
        display: flex;
        flex-direction: column; }
      .section-title {
        h2 {
          color: $white; }

        .subdesc {
          color: $white;
          opacity: .75; } }
      .btn-arrow {
        max-width: max-content;
        color: $white;
        border-bottom: 1px solid $white;
        margin-top: auto;
        &:after {
          background: url(../images/ic-arrow-right-long.svg);
          background-repeat: no-repeat;
          background-size: contain;
          background-position: center; }
        &:hover {
          border-color: $white; } } } }

  &-solution {
    background: #9CC9CE;
    .solution-wrap {
      margin-bottom: rem(-40); }
    .card-solution {
      border-top: 1px solid rgba(31, 31, 31, 0.25);
      padding-top: rem(24);
      margin-bottom: rem(40);
      b {
        color: $dark;
        padding-right: 4px;
        .svg {
          width: rem(24);
          height: rem(24);
          display: inline-flex;
          margin-right: rem(8); } } } }

  &-client {
    .client {
      &-wrap {
        .col-lg-3 {
          border-right: 1px solid rgba(27, 21, 54, 0.1);
          border-top: 1px solid rgba(27, 21, 54, 0.1);
          &:nth-child(4n) {
            border-right: 0; }
          &:nth-child(-n+4) {
            border-top: 0; } } } }
    .client-logo {
      padding: rem(40);
      img {
        width: auto;
        height: rem(64);
        margin: 0 auto;
        object-fit: contain;
        object-position: center; } } }

  &-someproduct {
    background: #F7FCFC;
    .prevnext {
      display: flex;
      gap: rem(16);
      justify-content: flex-end;
      align-items: flex-end;
      height: 100%;
      .prdSwiper-next,
      .prdSwiper-prev {
        width: rem(48);
        height: rem(48);
        border: 1px solid $primary;
        background-color: $white;
        background-image: url(../images/ic-arrow-right-long-blue.svg);
        background-position: center;
        background-repeat: no-repeat;
        background-size: rem(24);
        display: block;
        border-radius: 100px;
        &.swiper-button-disabled {
          opacity: .5; }
        &:hover {
          cursor: pointer; } }
      .prdSwiper-prev {
        transform: rotate(-180deg); } }

    .section-title {
      padding-top: rem(48);
      border-top: 1px solid $primary; }
    .more {
      text-align: center;
      margin-top: rem(48); } }

  @include maxtablet() {
    &-masthead {
      &-swiper {
        .swiper {
          &-slide {
            &:before {
              padding: 115% 0 0; } } } } }
    &-category {
      .section-title {
        .row {
          .col-lg-6 {
            &:first-child {
              margin-bottom: 24px; }
            &:last-child {
              justify-content: flex-start; } } } }

      &-wrap {
        .card-category-img:before {
          padding: 71.425% 0 0; } } }
    &-aboutus {
      padding: rem(48) 0;
      &-content {
        min-height: rem(436.03);
        padding: 0; }
      &-bg {
        &:after {
          background: #000;
          opacity: .5; } } }
    &-someproduct {
      .product-card {
        max-width: 100%; } }
    &-client {
      .client {
        &-wrap {
          .col-lg-3 {
            &:nth-child(4n) {
              border-right: 1px solid rgba(27, 21, 54, 0.1); }
            &:nth-child(2n) {
              border-right: 0; }
            &:nth-child(-n+4) {
              border-top: 1px solid rgba(27, 21, 54, 0.1); }
            &:nth-child(-n+2) {
              border-top: 0; } } } } } }

  @include mobile {
    &-masthead {
      max-height: 600px;
      &-content {
        padding-bottom: 24px;
        .container {}
        .swiper-pagination {
          margin-top: 56px; } }
      &-swiper {
        .swiper-slide:before {
          padding: 191% 0 0; } } }
    &-client {
      .client {
        &-wrap {
          .col-lg-3 {
            flex: 0 0 50%;
            max-width: 50%; }
          .client-logo {
            padding: 16px; } } } }
    &-category {
      .section-title {
        padding-top: 32px; }
      &-wrap {
        .col-md-6 {
          flex: 0 0 50%;
          max-width: 50%;
          padding-left: 6px;
          padding-right: 6px;
          margin-bottom: 12px; }
        .card-category {
          &-img:before {
            padding: 128.2% 0 0; }
          &-name {
            padding: 12px;
            display: block;
            h4 {
              margin-bottom: 8px; } } } } }
    &-aboutus {
      padding: 48px 0; }
    &-someproduct {
      .section-title {
        .col-md-8 {
          margin-bottom: 24px; } }
      .prevnext {
        justify-content: flex-start; }
      .row {
        margin-left: -8px;
        margin-right: -8px;
        .col-lg-3 {
          padding-left: 8px;
          padding-right: 8px;
          margin-bottom: 16px !important; } } } } }

// Produk
.produk {
  padding-top: rem(98);
  &-masthead {
    position: relative;
    &:before {
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background: linear-gradient(0deg, rgba(2, 32, 45, 0.30) 0%, rgba(2, 32, 45, 0.30) 100%);
      z-index: 3; }
    &-bg {
      height: 100%;
      @include imgBg(33.4% 0 0); }
    &-content {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 4;
      display: flex;
      align-items: center;
      h1 {
        color: $white;
        margin-bottom: 0;
        @include font-500;
        max-width: rem(846);
        margin: 0 auto;
        text-align: center; } } }
  &-kategori {
    position: absolute;
    bottom: rem(28);
    left: 0;
    width: 100%;
    &-wrap {
      width: 100%;
      display: flex;
      align-items: center;
      gap: 8px;
      position: relative;
      .kategori-item {
        padding: rem(6) rem(12);
        @include font-500;
