        max-width: max-content;
        &.active {
          padding-left: rem(26);
          background: $white;
          border-color: $white;
          color: $primary;
          &:before {
            content: "";
            width: 6px;
            height: 6px;
            border-radius: 50px;
            position: absolute;
            top: rem(16);
            left: rem(12);
            background: $primary; } }
        &:hover {
          text-decoration: none;
          background: $white;
          border-color: $white;
          color: $primary; } } }
    &-bg {
      @include imgBg(53% 0 0); }

    .box-swiper {
      width: 100%;
      overflow: hidden; }
    .swiper-button-prev,
    .swiper-button-next {
      width: rem(28);
      height: rem(28);
      background: $white;
      border-radius: 50px;
      border: 1px solid $primary;
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      top: rem(26);
      &::after {
        width: rem(12);
        height: rem(12);
        font-size: 12px;
        line-height: 12px; }
      &.swiper-button-disabled {
        display: none; } }
    .swiper-button-prev {
      left: rem(-4); }
    .swiper-button-next {
      right: rem(-4); } }
  &-list {
    background: #F7FCFC;
    &-filter {
      margin-bottom: rem(40);
      b {
        @include font-600;
        color: $dark; }
      .filter {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        gap: 8px;
        span {
          flex: 0 0 rem(158);
          max-width: rem(158); }
        .select {
          flex: 0 0 rem(240);
          max-width: rem(240);
          button.btn,
          button {
            padding: rem(6) rem(16);
            border: 1px solid $light;
            border-radius: 50px;
            background: $white;
            color: $dark; } }
        .bootstrap-select {
          .dropdown-menu.show {
            min-width: auto;
            width: 100%;
            max-width: rem(240); } }
        &.filter-mobile {
          display: none; } } } }
  &-wrap {
    margin-bottom: rem(-24);
    .col-lg-3 {
      margin-bottom: rem(24); } }

  @include maxtablet {
    &-masthead {
      height: rem(400); } }
  @include mobile {
    &-kategori-wrap {
      opacity: 0;
      visibility: hidden; }
    &-list {
      &-filter {
        .col-lg-3 {
          display: none; }
        .filter {
          display: block;
          & > span {
            display: block;
            margin-bottom: 8px; }
          .select {
            max-width: 100%;
            button {
              padding: 12px !important; }
            .dropdown-menu.show {
              max-width: 100%; } }
          &.filter-mobile {
            display: block;
            margin-bottom: 12px; } } } }
    &-wrap {
      & > .row {
        margin-left: -8px;
        margin-right: -8px; }
      .col-md-6 {
        flex: 0 0 50%;
        max-width: 50%;
        padding-left: 8px;
        padding-right: 8px;
        margin-bottom: 16px; }
      .product-card {
        height: 100%;
        background: #FFF; } } } }


.produk-detail {
  &-top {}

  &-photo {
    position: relative;
    .photo-thumbnail {
      overflow: hidden;
      position: absolute;
      left: rem(24);
      top: calc(50% - #{rem(138)});
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-direction: column;
      z-index: 3;
      &.no-slider {
        .box-swiper {
          .swiper-wrapper {
            justify-content: center; } } }
      .box-swiper {
        position: relative;
        height: 228px;
        overflow: hidden; }
      .swiper-wrapper {}
      .swiper-slide {
        width: rem(48);
        height: rem(48);
        box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.10);
        border: 1px solid rgba(78, 94, 101, 0.3);
