          display: block;
          width: 100%;
          height: 100%;
          object-fit: cover;
          object-position: center; }
        &.selected {
          border-color: $primary; }
        &:hover {
          cursor: pointer; } }
      .prev-sliderV,
      .next-sliderV {
        width: rem(24);
        height: rem(24);
        position: relative;
        z-index: 4;
        .svg,
        img {
          width: rem(24);
          height: rem(24); }
        &.swiper-button-disabled {
          opacity: .3; }
        &:hover {
          cursor: pointer; } }
      .prev-sliderV {
        transform: rotate(-180deg); } }
    .box-img {
      @include imgBg(100% 0 0); } }
  &-info {
    padding-left: rem(56);
