      margin-bottom: rem(24); }
    h1 {
      color: $dark;
      font-size: rem(28);
      line-height: 120%;
      margin-bottom: rem(24); }
    hr {
      margin: rem(24) 0;
      background: transparent;
      border-top: 1px dashed $light; }
    .moq-list {
      margin-bottom: rem(24);
