        padding: rem(12) rem(16);
        border: 1px solid $light;
        display: flex;
        align-items: center;
        gap: 8px;
        &:not(:last-of-type) {
          margin-bottom: rem(12); }
        small {
          span {
            color: $primary; } }
        .price {
          color: $primary;
          font-size: rem(24);
          line-height: 150%;
          @include font-500;
          &.red {
            color: $burgundy; }
          .regular-price {
            font-size: rem(16);
            line-height: 150%;
            color: $slate;
            padding: 0 8px;
            letter-spacing: -0.32px;
            text-decoration: line-through; }
          .discount {
            padding: 0 5px;
            font-size: rem(12);
            line-height: rem(20);
            color: $white;
            background: #FF5C5C;
            border-radius: 6px;
            display: inline-block;
            vertical-align: middle; } } } }
    .moq-notes {
      font-size: rem(12);
      line-height: 150%;
      letter-spacing: -.24px;
      display: block;
      color: $muted;
      padding: rem(16) 0 rem(24);
      border-bottom: 1px dashed $light; }
    .despro-right {
      & > b {
        display: block;
        margin-bottom: rem(16); }
      article {
        margin-bottom: rem(24);
        // // max-height: 3em
        // overflow: hidden
        // max-height: 3em
 } } }        // overflow: hidden
  &-desc {
    position: relative;
    border-top: 1px solid $light;
    &:after {
      content: "";
      width: 50%;
      height: 100%;
      background: linear-gradient(180deg, #F7FCFC 0%, rgba(247, 252, 252, 0.00) 100%);
      position: absolute;
      top: 0;
      right: 0; }
    .container {
      position: relative;
      z-index: 5; }
    .row {
      .col-lg-6:last-child {
        display: flex;
        justify-content: flex-end; } }
    .desc-left {
      max-width: rem(560);
      article {
        margin-bottom: rem(40); }
      h3 {
        @include font-600;
        color: $dark;
        font-size: rem(20);
        line-height: 120%;
        margin-bottom: rem(16); }
      .ecommerce {
        &-list {
          max-width: rem(456); }
        &-item {
          padding: rem(23.1) rem(32);
          background: $white;
          border: 1px solid $light;
          display: flex;
          position: relative;
          transition: all .3s ease-in-out;
          img {
            width: auto;
            height: rem(32);
            object-fit: contain; }
          &:after {
            content: "";
            width: rem(20);
            height: rem(20);
            background: url(../images/ic-arrow-right-long-light.svg);
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center;
            position: absolute;
            top: calc(50% - #{rem(10)});
            right: rem(20); }
          &:not(:last-child) {
            margin-bottom: rem(12); }
          &:hover {
            border-color: $primary;
            background: $white;
            box-shadow: 0px 8px 32px 0px rgba(0, 0, 0, 0.10);
            &:after {
              background: url(../images/ic-arrow-right-long-blue.svg);
              background-repeat: no-repeat;
              background-size: contain;
              background-position: center; } } } } }

    .desc-right {
      // padding-left: rem(80)
      width: 100%;
      max-width: rem(560);
      h3 {
        @include font-600;
        color: $dark;
        font-size: rem(20);
        line-height: 120%;
        margin-bottom: rem(16); }
      .or {
