          transform: rotate(-90deg); } } }
    &-info {
      padding: 48px 0;
      padding-left: 0; }

    &-desc {
      background: linear-gradient(180deg, #F7FCFC 0%, rgba(247, 252, 252, 0.00) 100%);
      &:after {
        display: none; }
      &-left {}
      .desc-left {
        .ecommerce-item {
          padding: 12px 24px;
          img {
            max-height: 24px; } } }
      .desc-right {
        .or {
          display: block; } } }

    .someproduct-wrap {
      .row {
        .col-lg-3 {
          margin-bottom: 24px;
          flex: 0 0 50%;
          max-width: 50%;
          .product-card-price {
            flex-wrap: wrap; } } } } } }

