.grid-masonry {
  &:after {
    content: '';
    display: block;
    clear: both; }
  .grid-sizer,
  .grid-item {
    width: 30.178%; } //31.826%//rem(338)
  .grid-item {
    float: left;
    margin-bottom: rem(24);
    position: relative;
    &:before {
      content: "";
      width: 100%;
      padding-top: 208.2875%; //197.6% //48.3%
      display: block; }
    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
      position: absolute;
      top: 0;
      left: 0; }
    &.dw {
      width: 62.5%; //65.913% //rem(700)
      &:before {
        content: "";
        width: 100%;
        padding-top: 51.43%; //50%
        display: block; } }
    &.sq {
      &:before {
        padding-top: 106.51%; } } //94.676% //100%
    &:after {
      content: "";
      width: 100%;
      height: 100%;
      background: linear-gradient(0deg, rgba(2, 32, 45, 0.30) 0%, rgba(2, 32, 45, 0.30) 100%);
      position: absolute;
      top: 0;
      left: 0;
      opacity: 0;
      visibility: hidden;
      transition: all .2s ease-in-out; }
    .content {
      width: 100%;
      padding: rem(24);
      color: $white;
      position: absolute;
      left: 0;
      bottom: 0;
      z-index: 3;
      transition: all .3s ease-in-out;
      transform: translateY(10px);
      opacity: 0;
      visibility: hidden;
      h3 {
        font-size: rem(24);
        margin-bottom: rem(8); } }
    &:hover {
      cursor: pointer;
      &:after {
        opacity: 1;
        visibility: visible; }
      .content {
        opacity: 1;
        visibility: visible;
        transform: translateX(0); } } }
  @include maxtablet {
    .grid-sizer,
    .grid-item {
      width: calc((100% / 3) - 24px);
      margin-bottom: 16px;
      &:after {
        opacity: 1;
        visibility: visible; }
      .content {
        opacity: 1;
        visibility: visible;
        // color: $dark
        padding: 16px;
        transform: translateX(0);
        h3 {
          margin-bottom: 0;
          width: 100%;
          // overflow: hidden
 }          // text-overflow: ellipsis
        p {
          display: none; } } }

    .grid-item.dw {
      width: calc(((100% / 3) - 18px) * 2); } }
  @include mobile {
    .grid-sizer,
    .grid-item {
      width: calc((100% / 2) - 12px);
      height: 200px;
      .content {
        h3 {
          font-size: 18px !important; } } }
    .grid-item.dw {
      width: calc((100% / 2) - 12px); } } }

