/* Header */
.header {
  background: $white;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
  border-bottom: 1px solid #E6E8E9;  //#E6E8E9
  & > .container {}
  &-main {
    width: 100%;
    display: flex;
    align-items: center;
    padding: rem(24) 0;
    transition: all .3s ease-in-out;
    position: relative; }
  &-logo {
    margin-right: rem(64);
    img {
      width: auto;
      height: rem(48); } }
  &-menu {
    .main-menu {
      display: flex;
      align-items: center;
      gap: rem(24);
      .menu-item {
        position: relative;
        a {
          position: relative;
          z-index: 5; }
        &.has-sub {
          position: relative;
          display: flex;
          align-items: center;
          gap: 8px;
          & > a {
            &:after {
              content: "";
              width: rem(16);
              height: rem(16);
              background-image: url(../images/ic-carret-down-blue.svg);
              background-repeat: no-repeat;
              background-position: center;
              display: inline-block;
              vertical-align: middle;
              transition: all .3s ease-in-out;
              margin-left: 8px; } }
          &:hover {
            & > a {
              &:after {
                transform: rotate(-180deg); } }
            @include responsive(0, 1100px) {
              .submenu {
                opacity: 1;
                visibility: visible; } } } }
        &:hover,
        &.active {
          cursor: pointer;
          & > a {
            text-decoration: none;
            color: $primary; }
          &:before {
            content: "";
            width: 100%;
            height: rem(61);
            position: absolute;
            top: 0;
            left: 0;
            border-bottom: 2px solid $primary;
            transition: all .3s ease-in-out; } } }
      .showmobile {
        padding-top: 24px;
        display: none;
        @include maxtablet {
          display: block; }
        @extend .container;
        .header-search {
          .searchform {
            max-width: 100%;
            opacity: 1;
            visibility: visible;
            margin: 0; }
          .sc-trigger {
            .svg {
              path {
                fill: $primary; } } } }
        .header-btnauth {
          span {
            padding-left: 8px; } }
        .ppuser {
          span {
            display: inline-block;
            vertical-align: middle;
            width: rem(40);
            height: rem(40);
            border: 1px solid $primary;
            text-align: center;
            @include font-500;
            font-size: 16px;
            letter-spacing: -.32px;
            border-radius: 50px;
            overflow: hidden;
            line-height: rem(40);
            background: rgba(7, 82, 114, 0.1);
            color: $blue; } }
        &.profile {
          @include maxtablet {
            display: none;
            justify-content: space-between;
            align-items: center; }
          .btn-link {
            color: #1A231E; } } } } }

  &-right {
    display: flex;
    align-items: center;
    margin-left: auto;
    gap: rem(24);
    .header-logedinuser {
      display: none; }
    a.header-btnauth,
    .header-btnauth > a {
      padding: rem(8) rem(16);
      border: 1px solid $primary;
      background: $white;
      display: flex;
      align-items: center;
      width: 100%;
      gap: rem(8);
      color: $primary;
      &:hover {
        text-decoration: none !important;
        background: $primary;
        color: $white;
        .svg {
          path {
            fill: $white; } } }
      img {
        width: rem(20);
        height: rem(20); } } }

  &-search {
    min-width: rem(20);
    // // display: flex
    // align-items: center
    position: relative;
    .searchform {
      max-width: 0;
      opacity: 0;
      visibility: visible;
      margin: 0;
      .form-control {
        border: 0;
        padding: 0;
        border-bottom: 1px solid $primary;
        // background: transparent
        width: 100%; } }

    .sc-trigger {
      position: absolute;
      display: block;
      top: rem(8);
      right: rem(8);
      width: rem(20);
      height: rem(20);
      z-index: 5;
      &:hover {
        cursor: pointer; } }
    .icon-search {}
    &.show {
      .searchform {
        width: 100%;
        max-width: rem(264);
        opacity: 1;
        visibility: visible; }
      .form-control {
        padding: rem(12); } } }
  &:not(.header-alt) {
    .header {
      &-right {
        .sc-trigger {
          .icon-search {
            path {
              fill: $primary; } } }
        a.header-btnauth,
        .header-btnauth > a {
          .svg {
            path {
              fill: $primary; } }
          &:hover {
            .svg path {
              fill: $white; } } } } } }
  &.header-alt {
    background: transparent;
    border-bottom: 1px solid rgba(230, 232, 233, .25); //#E6E8E9
    &.svg {
      path {
        fill: #FFF; } }
    .header {
      &-menu {
        .main-menu {
          .menu-item {
            a {
              color: $white; }
            &.has-sub {
              & > a {
                &:after {
                  background-image: url(../images/ic-carret-down-white.svg);
                  background-repeat: no-repeat;
                  background-position: center; } } }
            &:hover,
            &.active {
              &:before {
                border-color: #FFF; } } }
          .submenu {
            a {
              color: $slate;
              &:hover {
                color: $primary; } } } } }

      &-right {
        a.header-btnauth,
        .header-btnauth > a {
          border: 1px solid $white;
          background: rgba(255, 255, 255, 0.10);
          color: $white;
          a {
            color: $white; }
          .svg {
 }            // margin-right: rem(8)
          &:hover {
            text-decoration: none;
            background: $white;
            color: $primary;
            border-color: $white;
            .svg {
              path {
                fill: $primary; } } } } } }
    .mobile-menu {
      span {
        background: #FFF; } } }
  &.fixed {
    background: $white;
    .header {
      &-main {
        padding: rem(24) 0; }
      &-menu {
        .main-menu {
          .menu-item {
            a {
              color: $slate; }
            &.has-sub {
              & > a {
                &:after {
                  background-image: url(../images/ic-carret-down-blue.svg);
                  background-repeat: no-repeat;
                  background-position: center; } } }
            &:hover {
              &:after {
                height: rem(61); } }
            &:hover,
            &.active {
              &:before {
 } }                // height: rem(53)
            &.has-sub {
              &:hover,
              &.active {
                & > a {
                  &:before {
                    height: rem(78); } } } } } } }
      &-right {
        a.header-btnauth,
        .header-btnauth > a {
          padding: rem(8) rem(16);
          border: 1px solid $primary;
          background: $white;
          a {
            color: $primary; }
          .svg {
            path:last-child {
              fill: $primary; } }
          &:hover {
            background: $primary;
            color: $white;
            .svg {
              path:last-child {
                fill: $white; } } } }
        .icon-search {
          path:last-child {
            fill: $primary; } } } }
    &.header-alt {
      border-color: $light;
      .mobile-menu {
        span {
          background: $primary; } }
      .header-menu {
        .main-menu {
          .menu-item {
            &:hover,
            &.active {
              &:before {
                border-color: $primary; } } } } }
      .header {
        &-right {
          a.header-btnauth,
          .header-btnauth > a {
            color: $primary;
            &:hover {
              background: $primary;
              color: $white !important;
              .svg {
                path:last-child {
                  fill: $white !important; } } } } } } }
    .submenu {
 } }      // top: calc(100% + #{rem(34)})
  .submenu {
    padding: rem(24);
    background: $white;
    position: absolute;
    left: 0;
    top: calc(100% + #{rem(50)});
    transition: all .3s ease-in-out;
    opacity: 0;
    visibility: hidden;
    box-shadow: 0px 10px 24px 0px rgba(25, 32, 34, 0.15);
    &:before {
      content: "";
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 0 10px 10px 10px;
      border-color: transparent transparent #ffffff transparent;
      position: absolute;
      top: -10px;
      left: rem(24); }
    &.md {
      width: rem(584); }
    &-title {
      padding-bottom: rem(16);
      margin-bottom: rem(16);
      border-bottom: 1px dashed $light; }
    &-list {
      column-count: 2;
      column-gap: rem(16);
      li {
        padding: rem(8) 0;
        a {
          position: relative;
          color: $slate;
          &:after {
            content: "";
            width: rem(20);
            height: rem(20);
            position: absolute;
            top: 0;
            left: calc(100% + #{rem(8)});
            background: url(../images/ic-arrow-right-long-blue.svg) no-repeat;
            background-size: contain;
            background-position: center;
            transition: all .5s ease;
            opacity: 0;
            visibility: hidden; } }
        &:hover {
          a {
            color: $primary;
            text-decoration: none;
            &:after {
              left: calc(100% + #{rem(10)});
              opacity: 1;
              visibility: visible; } } } } }
    @include maxtablet {
      padding: 16px 0 0 0;
      .submenu-title {
        display: none; } } }


  &.usersignin {
    .header {
      &-right {
        .header-logedinuser {
          display: block;
          .ppuser {
            span {
              display: inline-block;
              vertical-align: middle;
              width: rem(40);
              height: rem(40);
              border: 1px solid $primary;
              text-align: center;
              @include font-500;
              font-size: 16px;
              letter-spacing: -.32px;
              border-radius: 50px;
              overflow: hidden;
              line-height: rem(40);
              background: rgba(7, 82, 114, 0.1);
              color: $blue; } }
          .dropdown {
            .dropdown-toggle {
              &::after {
                width: rem(16);
                height: rem(16);
                background: url(../images/ic-carret-down-blue.svg) no-repeat center;
                border: 0;
                vertical-align: middle;
                transition: all .3s ease-in-out; } }
            &.show {
              .dropdown-toggle {
                &::after {
                  transform: rotate(-180deg); } } } } }
        a.header-btnauth,
        .header-btnauth > a {
          display: none; } } }
    &.header-alt {
      .header-right {
        .header-logedinuser {
          .ppuser {
            span {
              border-color: rgba(255, 255, 255, 0.5);
              color: $white;
              background: rgba(255, 255, 255, 0.1); } }
          .dropdown {
            .dropdown-toggle::after {
              background: url(../images/ic-carret-down-white.svg) no-repeat center; } } } } }
    &.fixed {
      .header-right {
        .header-logedinuser {
          .ppuser {
            span {
              border-color: $primary;
              color: $primary;
              background: rgba(7, 82, 114, 0.1); } }
          .dropdown {
            .dropdown-toggle::after {
              background: url(../images/ic-carret-down-blue.svg) no-repeat center; } } } } }
    .header-menu {
      .main-menu {
        .showmobile {
          &.header-btnauth {
            display: none; }
          &.profile {
            @include maxtablet {
              display: flex; } } } } } }

  @include maxtablet() {
    &-menu {
      display: none;
      .main-menu {
        .menu-item {
          margin-bottom: 0 !important;
          border-bottom: 1px solid #E5E5E5;
          & > a {
            display: inline-block;
            padding: 16px 40px; }
          &.active,
          &:hover {
            &:before {
              display: none !important; } }
          &.has-sub {
            position: relative;
            & > a {
              max-width: calc(100% - 72px); }
            &:before {
              content: "";
              position: absolute;
              top: 0;
              left: 0;
              width: 100%;
              height: 56px;
              background: #F0F6F8;
              opacity: 0;
              visibility: hidden; }
            &:after {
              content: "";
              position: absolute;
              top: 20px;
              right: 40px;
              width: rem(16);
              height: rem(16);
              background-image: url(../images/ic-carret-down-blue.svg);
              background-repeat: no-repeat;
              background-position: center;
              display: inline-block;
              vertical-align: middle;
              transition: all .3s ease-in-out;
              margin-left: 8px; }
            a:after {
              display: none;
              &:after {
                transform: rotate(180deg); }
              .submenu {
                margin-bottom: 16px;
                &:before {
                  display: none; } } }
            &.sub-menu-open {
              &:before {
                opacity: 1;
                visibility: visible; }
              &:after {
                transform: rotate(180deg); } }

            .submenu {
              padding: 16px 40px;
              transition: height .3s ease;
              &:before {
                display: none; } } } } } }

    &-right {
      .header {
        &-search {
          display: none; } }

      a.header-btnauth,
      .header-btnauth a {
        display: none; } }

    .mobile-menu {
      display: flex; }
    &.usersignin {
      .header-logedinuser {
        display: none !important; } } }

  @include mobile {
    &-menu {
      .main-menu {
        .menu-item {
          & > a {
            padding: 16px 24px; }
          &.has-sub {
            &:after {
              right: 24px; }
            .submenu {
              padding: 16px 24px; } } } } } } }


.mobile-menu {
  width: rem(24);
  margin: 0 auto;
  cursor: pointer;
  height: rem(24);
  z-index: 5;
  transition: .25s ease-in-out;
  position: absolute;
  top: calc(50% - 12px);
  right: 0;
  // display: flex
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: rem(8);
  display: none;
  span {
    display: block;
    // position: absolute
    height: 1px;
    width: 18px;
    border-radius: 20px;
    background: $primary;
    opacity: 1;
    left: 0;
    transform :rotate(0deg) {}
    //transition: opacity .5s .5s,top .5s .4s,transform .5s
    transition: all .4s ease;
    // &:nth-child(1)
    //   top: rem(5)
    // &:nth-child(2)
 } }    //   top: rem(12)
body {
  &.hovered-submenu {
    .header-alt {
      background: $white;
      .header {
        &-menu {
          .main-menu {
            .menu-item {
              & > a {
                color: $primary; }
              &.has-sub {
                & > a {
                  &:after {
                    content: "";
                    width: rem(16);
                    height: rem(16);
                    background-image: url(../images/ic-carret-down-blue.svg);
                    background-repeat: no-repeat;
                    background-position: center; } } } } } }
        &-right {
          a.header-btnauth,
          .header-btnauth > a {
            padding: rem(8) rem(16);
            border: 1px solid $primary;
            background: $white;
            color: $primary;
            img,
            .svg {
              width: rem(20);
              height: rem(20);
              path {
                fill: $primary; } }
            a {
              display: flex;
              align-items: center;
              width: 100%;
              gap: rem(8);
              color: $primary;
              &:hover {
                text-decoration: none; }
              img,
              .svg {
                width: rem(20);
                height: rem(20);
                path {
                  fill: $primary; } } } } } } } }
  &.search-show {
    a.header-btnauth,
    .header-btnauth > a {
      flex: 0 0 rem(186);
      max-width: rem(186); }
    .header-search {
      max-width: calc(100% - rem(190));
      .searchform {
        max-width: 100%;
        opacity: 1;
        visibility: visible;
        .form-control {
          background: transparent;
          color: $slate;
          &::placeholder {
            color: #CFD1D1;
            opacity: 1;
            font-weight: 400;
            letter-spacing: -0.32px; }
          &:-ms-input-placeholde {
            color: #CFD1D1;
            opacity: 1;
            font-weight: 400;
            letter-spacing: -0.32px; }
          &::-ms-input-placeholder {
            color: #CFD1D1;
            opacity: 1;
            font-weight: 400;
            letter-spacing: -0.32px; } } } } }
  &.menu-open {
    height: 100vh;
    overflow: hidden;
    .header {
      background: #FFF;
      .mobile-menu {
        span {
          background: $primary;
          &:nth-child(1) {
            transform: rotate(130deg); }
          &:nth-child(2) {
            transform: rotate(45deg);
            margin-top: -8px; } } } }
    .header-menu {
      display: block;
      position: fixed;
      width: 100%;
      height: 100%;
      top: rem(80);
      left: 0;
      background: #FFF;
      .main-menu {
        padding-top: rem(24);
        padding-bottom: rem(24);
        display: block;
        // @extend .container
        .menu-item {
          a {
            color: $primary !important; }
          &:not(:last-child) {
            margin-bottom: rem(16); }
          &.has-sub {
            display: block;
            a {
              &:after {
                background-image: url(../images/ic-carret-down-blue.svg) !important;
                background-repeat: no-repeat;
                background-position: center; } }
            &.sub-menu-open {
              .submenu {
                width: 100%;
                position: relative;
                box-shadow: none;
                opacity: 1;
                visibility: visible; } } } } } } } }
